.stats {
  background-color: var(--black);

  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }

  &__content .section__header {
    margin-bottom: 1rem;
  }

  &__content .para {
    margin-bottom: 2rem;
    color: var(--text-light);
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }

  &__card span {
    font-size: 1.75rem;
    color: var(--primary-color);
  }

  &__card h4 {
    font-size: 2rem;
    font-weight: 700;
  }

  &__card p {
    font-weight: 500;
  }

  &__image {
    display: grid;
    gap: 2rem;
  }

  &__image img {
    max-width: 450px;
    margin: auto;
    border-radius: 5px;
  }
}
