.header {
  background-image: linear-gradient(to right,
      rgba(0, 0, 0, .5),
      rgba(0, 0, 0, .8)),
    url("../../img/header.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

}

.nav {
  max-width: var(--max-width);
  margin: auto;
  padding: 2rem 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__logo a {
    font-size: 2rem;
    font-weight: 700;
    color: var(--white);
   text-decoration: none;
  }

  &__links {
    display: flex;
    align-items: center;
    gap: 2rem;
  }

  &__link a {
    text-transform:capitalize;
    text-decoration: none;
    padding: 5px;
    font-size: 1rem;
    font-weight: 500;
    color: var(--white);
    transition: 0.3s;
  }

  &__link a:hover {
    color: var(--primary-color);
  }
  &__menu-btn{
    display: none;
    font-size: 1.5rem;
  }
}
.header__container {
  padding-block: 5rem 12rem;
}
.header__container h1{
  max-width: 900px;
  margin-inline: auto;
  margin-bottom: 2rem;
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
}
.header__container .btn{
  margin: auto;
}
img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
