.section__header {
font-size: 2.5rem;
font-weight: 600;
}
.section__subheader{
  position: relative;
  isolation: isolate;
  margin-bottom: 1rem;
  padding-left: 20px;
  font-size: 1.2rem;
  font-weight: 500;
}
.section__subheader::after{
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 45px;
  aspect-ratio: 1;
  background-color: var(--primary-color);
  z-index: -1;
}
.btn{
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 0.75rem 1.5rem;
  outline: none;
  font-size: 1rem;
  color: var(--white);
  border-radius: 5px;
  cursor: pointer;
}
.btn__secondary{
  background-color: transparent;
  border: 1px solid var(--white);
}
.btn__primary{
  background-color: var(--primary-color);
  border: 1px solid var(--primary-color);
}
.btn span{
  font-size: 1.2rem;
  transition: 0.3s;
}
.btn:hover span {
transform: translateX(5px);
}
