.banner {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url('../img/banner.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;


  &__content {
    max-width: 880px;
  }

  &__content .section__header {
    margin-bottom: 1rem;
  }

  &__content .para {
    max-width: 600px;
    margin-bottom: 2rem;
  }

  &__bns {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}


