.about {
  background-color: var(--secondary-color);

  &__container {
    padding-block: 0;
  }

  &__grid {
    padding: 2rem;
    display: grid;
    grid-template-columns: 1.5fr 1fr;
    gap: 4rem;
    align-items: center;
    background-color: var(--secondary-color-light);
    transform: translateY(-5rem);
    border-radius: 10px;
  }

  &__content .section__header {
    margin-bottom: 1rem;
  }

  &__content .para {
    color: var(--text-light);
  }

  &__list {
    display: grid;
    gap: 2rem;
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 2rem;
  }

  &__item span {
    padding: 13px 20px;
    font-size: 1.75rem;
    color: var(--primary-color);
    background-color: var(--secondary-color);
    border-radius: 5px;
  }

  &__item h4 {
    font-size: 1.2rem;
    font-weight: 600;
  }
}
